import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { HikingDetails } from '../hiking-details/hiking-details';
import { Hike } from '../../model/hike';
import { Steps } from '../../model/steps';

@Component({
  selector: 'page-list',
  templateUrl: 'hikings-list.html'
})
export class HikingsList {

  public hikes : Array<Hike>;

  constructor(public navCtrl: NavController, public navParams: NavParams) {
    let testSteps = new Steps();
    testSteps.add("step 1", 150.23, 180.46);
    testSteps.add("step 2", 200.55, 170.56);
    this.hikes = [
      new Hike('hiking1', {
        latitude: 101.43,
        longitude: 118.10
      }, {
        latitude: 231.23,
        longitude: 120.12
      }, testSteps),
      
      new Hike('hiking2', {
        latitude: 231.23,
        longitude: 120.12
      }, {
        latitude: 101.43,
        longitude: 118.10
      }, new Steps())
  ];
}

  itemTapped(evt, item, hiking) {
    this.navCtrl.push(HikingDetails, {
      hiking: hiking
    });
  }
}



